import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import AssetServiceTypeCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetServiceType/actions";
import assetCategoryActions from "../../../../redux/asset/assetCategory/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/asset-service-types";
const { storeAssetCategory } = assetCategoryActions;

class CreateAssetServiceType extends Component {
  state = {
    visibleAddAssetCategory: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createAssetServiceType } = this.props;
    createAssetServiceType();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetServiceType.error) {
      notification("error", this.props.assetServiceType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetServiceType.success) {
      this.props.history.replace(redirectTo);
    }

    // Asset Category
    if (this.props.assetCategory.error) {
      notification("error", this.props.assetCategory.error);
      this.props.clearError();
    }

    if (this.props.assetCategory.success) {
      const { createAssetServiceType, clearSuccess } = this.props;
      notification("success", this.props.assetCategory.success);
      this.handleModalAddAssetCategory();
      clearSuccess();
      createAssetServiceType();
    }
  }

  handleModalAddAssetCategory = () => {
    this.setState({
      visibleAddAssetCategory: !this.state.visibleAddAssetCategory
    });
  };

  handleSubmit = value => {
    const { storeAssetServiceType } = this.props;
    storeAssetServiceType(value);
  };

  handleSubmitAssetCategory = value => {
    const { storeAssetCategory } = this.props;
    storeAssetCategory(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetServiceTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <AssetServiceTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            assetServiceType={this.props.assetServiceType}
            // Asset Category
            assetCategory={this.props.assetCategory}
            visibleAddAssetCategory={this.state.visibleAddAssetCategory}
            handleModalAddAssetCategory={this.handleModalAddAssetCategory}
            handleSubmitAssetCategory={this.handleSubmitAssetCategory}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    assetServiceType: state.assetServiceType,
    assetCategory: state.assetCategory
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storeAssetCategory })
)(CreateAssetServiceType);
