import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Col, InputNumber, Checkbox } from "antd";
import getLanguage from "../../../App/MyLanguange";
import ModalAddAssetCategory from "./modalAddAssetCategory";

const { Option } = Select;
const InputGroup = Input.Group;

class AssetServiceTypeCreateForm extends Component {
  state = {
    stop: false,
    check_code: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["asset_service_type_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { assetServiceType } = this.props;
    const { loading, asset_categories } = assetServiceType;
    const error = assetServiceType.errorField
      ? assetServiceType.errorField
      : [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <>
        <ModalAddAssetCategory
          handleModal={this.props.handleModalAddAssetCategory}
          assetCategory={this.props.assetCategory}
          //assetServiceType={this.props.assetServiceType}
          submit={this.props.handleSubmitAssetCategory}
          visible={this.props.visibleAddAssetCategory}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetCategories.pageHeader" />}
              >
                {getFieldDecorator("id_asset_category", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={loading}
                    showSearch
                    optionFilterProp="children"
                    style={{ width: "100%" }}
                  >
                    {permissions.includes("Create Asset Category") ? (
                      <Option
                        key={"Create Asset Category"}
                        value={undefined}
                        onClick={() => this.props.handleModalAddAssetCategory()}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "assetCategories.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}

                    {asset_categories.map((data, key) => (
                      <Option key={key} value={data.id_asset_category}>
                        {data.asset_category_code +
                          " (" +
                          data.asset_category_name +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="assetServiceTypes.add.code" />}
                    validateStatus={
                      error.asset_service_type_code ||
                      getFieldError("asset_service_type_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.asset_service_type_code
                        ? error.asset_service_type_code
                        : getFieldError("asset_service_type_code")
                    }
                  >
                    {getFieldDecorator("asset_service_type_code", {
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetServiceTypes.add.name" />}
              >
                {getFieldDecorator("description", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetServiceTypes.intervalBy" />}
              >
                {getFieldDecorator("interval_by", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group
                    style={{ display: "flex" }}
                    buttonStyle="solid"
                    loading={loading}
                  >
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={1}
                    >
                      <IntlMessages id="time" />
                    </Radio.Button>
                    <Radio.Button
                      style={{ flex: "1", textAlign: "center" }}
                      value={2}
                    >
                      <IntlMessages id="parameter" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              {getFieldValue("interval_by") === 1 ? (
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetServiceTypes.intervalUnit" />}
                >
                  {getFieldDecorator("interval_unit", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group
                      style={{ display: "flex" }}
                      buttonStyle="solid"
                    >
                      <Radio.Button
                        style={{ flex: "1", textAlign: "center" }}
                        value="days"
                      >
                        <IntlMessages id="days" />
                      </Radio.Button>
                      <Radio.Button
                        style={{ flex: "1", textAlign: "center" }}
                        value="months"
                      >
                        <IntlMessages id="months" />
                      </Radio.Button>
                      <Radio.Button
                        style={{ flex: "1", textAlign: "center" }}
                        value="years"
                      >
                        <IntlMessages id="years" />
                      </Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              ) : (
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="assetServiceTypes.intervalUnit" />}
                >
                  {getFieldDecorator("interval_unit", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                      {
                        max: 15,
                        message: <IntlMessages id="maxLength15" />
                      }
                    ]
                  })(<Input />)}
                </Form.Item>
              )}

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="assetServiceTypes.intervalValue" />}
              >
                {getFieldDecorator("interval_value", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>
            </div>
          </div>

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "20px", marginLeft: "50px" }}
              loading={loading}
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedAssetServiceTypeCreateForm = Form.create({
  name: "create_asset_service_type"
})(AssetServiceTypeCreateForm);

export default WrappedAssetServiceTypeCreateForm;
